body, input {
	font-family: Roboto;
}

body {
	margin: 0;
}

#root {
	max-width: 1024px;
	margin: 50px auto;
	padding: 0 20px;
}

.generator {
	max-width: 630px;
	margin: 0 auto;
}

p {
	margin-top: 30px;
	font-size: 1.5em;
}

input[type="text"] {
	font-size: 1em;
	/*outline: none;*/
	width: 100%;
	max-width: 630px;
	height: 2.5em;
	box-sizing: border-box;
}

input[type="text"]:hover {
	box-shadow: none;
}

.radio {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
	grid-gap: 30px;
}

.generator-radio {
	width: 100%;
}

.generator-radio-inner-wrapper {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
}

.generator-radio input[type=radio] {
    display:none; 
}

.generator-radio input + label {
	display: inline-block;
	text-align: center;
	padding: 10px;
	cursor: pointer;
	border: 1px solid rgba(0,0,0,0.1);
}

.generator-radio input + label:first-of-type {
	border-radius: 6px 0 0 6px;
}

.generator-radio input + label:last-of-type {
	border-radius: 0 6px 6px 0;
}

.generator-radio input + label:hover {
	background: #ddd;
}

.generator-radio input:checked + label {
	background: #2196F3;
	box-shadow: 0 0 5px #2196F3;
	color: #FFF;
}

.code-container {
	margin-top: 50px;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
	grid-gap: 20px;
}

.code {
	padding: 0 20px;
	white-space: pre;
	overflow: scroll;
	cursor: text;
	background: #eee;
	height: 400px;
}