.social-checkboxes {
	margin-top: 30px;
	display: grid;
	grid-gap: 10px;
	grid-template-columns: repeat(auto-fill, 30px);
}

.container {
	position: relative;
	cursor: pointer;
	width: 30px;
	height: 30px;
	user-select: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
}

.container input {
	position: absolute;
	height: 0;
	width: 0;
}

.checkmark {
	position: absolute;
	top: 0;
	left: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	/*background: #ddd;*/
	border-radius: 6px;
	border: 1px solid rgba(0,0,0,0.1);
}

.container:hover input ~ .checkmark {
	background: #ddd;
}

.container input:checked ~ .checkmark {
  background-color: #2196F3;
  box-shadow: 0 0 5px #2196F3;
}

.container input:checked ~ .checkmark svg {
  fill: #fff;
}

.checkmark svg {
	fill: #000;
	width: 1.25em;
	height: 1.25em;
}